import React from 'react';
import {Element} from 'react-scroll';

export default function () {
  return (
    <Element name="contacts" id={"contacts"} className="container">
      <div className="row">
        <div className="col py-3">
          <h2 className="section-heading">
            Контакты
          </h2>
        </div>
      </div>
      <div className="row">
        <div className="col-12 col-lg-4 p-3">
          <h1 className="section-heading text-center">
            Avocado Language Centre
          </h1>
          <p className="text-center">
            <small>Центр изучения иностранных языков</small>
          </p>
          <hr/>
          <ul className="list-unstyled m-0">
            <li className={"contacts-item"}>
              <i className="contacts-item__icon fas fa-phone"/>
              <span className={"contacts-item__text"}>
                  +7 926 164 2584
                </span>
            </li>
            <li className={"contacts-item"}>
              <i className="contacts-item__icon far fa-address-card"/>
              <span className={"contacts-item__text"}>
                  град Московский, улица Радужная, дом 6 (вход со двора)
                </span>
            </li>
            <li className={"contacts-item"}>
              <i className="contacts-item__icon far fa-envelope"/>
              <a href="mailto:help@avocadocentre.ru"
                 className={"contacts-item__text"}
              >
                help@avocadocentre.ru
              </a>
            </li>
            <li className={"contacts-item"}>
              <i className="contacts-item__icon fab fa-instagram"/>
              <a href="https://www.instagram.com/avocado_centre/"
                 target={"_blank"}
                 className={"contacts-item__text"}
              >
                instagram.com/avocado_centre/
              </a>
            </li>
            <li className={"contacts-item"}>
              <i className="contacts-item__icon fab fa-vk"/>
              <a href="https://vk.com/avocadocentre"
                 target={"_blank"}
                 className={"contacts-item__text"}
              >
                vk.com/avocadocentre
              </a>
            </li>
          </ul>
        </div>
        <div className="col-12 col-lg-8 p-3">
          <iframe src={"/sw-ignore/map/map02-09-2018.html"} title="map" className="map"/>
        </div>
      </div>
    </Element>
  );
}



