import React from 'react';
import {scroller} from 'react-scroll';
import avocado from './assets/avocado.svg';

export default function () {
  const sctrollTo = to => () => {
    scroller.scrollTo(to, {
      smooth: true,
      duration: 500,
    });
  };

  return (
    <nav className="container navbar navbar-expand-lg navbar-light p-3">
      <a className="navbar-brand" href="/"><img src={avocado} className="header__logo"
                                                alt="avocado language centre logo"/></a>
      <button className="header-toggler navbar-toggler btn btn-light" type="button" data-toggle="collapse"
              data-target="#navbarToggler"
              aria-controls="navbarToggler" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className="collapse navbar-collapse" id="navbarToggler">
        <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
          <li className="nav-item">
            <a href="#plans"
               className={"nav-link"}
               onClick={sctrollTo('plans')}>
              Программы обучения
            </a>
          </li>
          <li className="nav-item">
            <a href="#contacts"
               className={"nav-link"}
               onClick={sctrollTo('contacts')}>
              Контакты
            </a>
          </li>
        </ul>

        {/*Ссылки на социальные сети*/}
        <div className={"contact-links"}>
          {/*Instagram*/}
          <a href="https://www.instagram.com/avocado_centre/"
             className={"contact-links__link"}
             target={"_blank"}
          >
            <i className="fab fa-instagram"/>
          </a>
          {/*VK*/}
          <a href="https://vk.com/avocadocentre"
             className={"contact-links__link"}
             target={"_blank"}
          >
            <i className="fab fa-vk"></i>
          </a>
          {/*Mail*/}
          <a href="mailto:help@avocadocentre.ru"
             className={"contact-links__link"}
            // target={"_blank"}
          >
            <i className="far fa-envelope"></i>
          </a>
        </div>
      </div>
    </nav>
  );
}


