import React from 'react';
import {Element} from 'react-scroll';

export default function () {
  const cardNameEnglish = <span>Английский язык<br/>для<br/></span>;
  const cardNameFrench = <span>Французский язык<br/>для<br/></span>;
  const plans = {
    english: [
      {
        name: cardNameEnglish,
        for: 'Дошкольников',
        lessonsCount: '8 занятий',
        price: 4000,
      },
      {
        name: cardNameEnglish,
        for: 'Начальной школы',
        lessonsCount: '8 занятий',
        price: 4800,
      },
      {
        name: cardNameEnglish,
        for: 'Средней школы',
        lessonsCount: '8 занятий',
        price: 4800,
      },
      {
        name: cardNameEnglish,
        for: 'Старшей школы',
        lessonsCount: '8 занятий',
        price: 5200,
      },
      {
        name: <span>Подготовка<br/>к сдаче<br/></span>,
        for: 'ОГЭ',
        lessonsCount: '8 занятий',
        price: 6400,
      },
      {
        name: <span>Подготовка<br/>к сдаче<br/></span>,
        for: 'ЕГЭ',
        lessonsCount: '8 занятий',
        price: 6400,
      },
      {
        name: <span>Книжный клуб<br/>для<br/></span>,
        for: 'Взрослых',
        lessonsCount: '4 занятия',
        price: 2400,
      },
    ],
    french: [
      {
        name: cardNameFrench,
        for: 'Дошкольников',
        lessonsCount: '8 занятий',
        price: 4800,
      },
      {
        name: cardNameFrench,
        for: 'Начальной школы',
        lessonsCount: '8 занятий',
        price: 5600,
      },
      {
        name: cardNameFrench,
        for: 'Средней школы',
        lessonsCount: '8 занятий',
        price: 6000,
      },
      {
        name: cardNameFrench,
        for: 'Взрослых',
        lessonsCount: '8 занятий',
        price: 6400,
      },
    ],
    italian: [
      {
        name: <span>Итальянский язык<br/>для<br/></span>,
        for: 'Взрослых',
        lessonsCount: '8 занятий',
        price: 6400,
      },
    ],
  };

  return (
    <Element name="plans" id={"plans"} className="container">
      <div className="row">
        <div className="col py-3">
          <h2 className="section-heading">
            Программы обучения
          </h2>
          {/*<hr/>*/}
        </div>
      </div>

      <div className="row justify-content-center">
        <div className="col-12 py-3">
          <h5 className="text-center">Английский язык</h5>
        </div>
        {plans.english.map(getTemplate)}
      </div>

      <div className="row justify-content-center">
        <div className="col-12 py-3">
          <h5 className="text-center">Французский язык</h5>
        </div>
        {plans.french.map(getTemplate)}
      </div>

      <div className="row justify-content-center">
        <div className="col-12 py-3">
          <h5 className="text-center">Итальянский язык</h5>
        </div>
        {plans.italian.map(getTemplate)}
      </div>

    </Element>
  );
}

function getTemplate(card, key) {
  return (
    <div className="plan col-12 col-sm-6 col-lg-4 col-xl-3 p-3" key={key}>
      <div className="card">
        <div className="text-center card-body">
          <h5 className="plan__title card-title">
            {card.name}
            <mark>{card.for}</mark>
          </h5>
          <p className="mb-2">
            {card.lessonsCount}
          </p>
          {/*<p className="mb-2">*/}
            {/*<small><s>{card.price} рублей</s> <span className="badge badge-primary text-white">-25%</span></small>*/}
          {/*</p>*/}
          <p className="card-text">
            <span className="lead">{card.price} рублей</span>
            <br/>
            <small>*Первое занятие бесплатно</small>
          </p>
          {/*<a href="#" className="btn btn-primary">Go somewhere</a>*/}
        </div>
      </div>
    </div>
  );
}



